import React from 'react'  

const Todo = ({todos}) => {
const deletediv = (e) => {
    e.target.parentElement.parentElement.parentElement.remove()
}
const deletediv2 = (e) => {
    e.target.parentElement.parentElement.remove()
}
const check2 = (e) => {e.target.parentElement.parentElement.parentElement.className = "change";}

    return(
        <div className="divs ">
    <li>{todos}</li>
    <div className="buttonsdiv">
    <button className="check-btn btns" ><i onClick={check2}class="far fa-check-circle"></i></button>
    <button className="del-btn btns"  onClick={deletediv2}><i onClick={deletediv} className="fas fa-trash-alt"></i></button>
    </div>
        </div>
    ) 
}

export default Todo