import React from 'react'

const SearchField = ({setInputText, setTodos, todos, inputText}) => {

     const inputTextHandler = (e) => {setInputText(e.target.value); console.log(inputText.length)};
     const submitTodoHandler = (e) => {
       e.preventDefault();
        if (inputText.length >= 1 ) {
       setTodos([
         ...todos,
         { text: inputText, completed: false, id: Math.random() }
       ]);
       setInputText("");}
       else alert ("Type Something Please")
     }
    
    return(
    <div>
      <input className="inputtext"type="search" placeholder="type here" onChange = {inputTextHandler} value={inputText}/>
      <button className="add-btn" onClick={submitTodoHandler}><i class="fas fa-plus"></i></button> 
    </div>
    )
}



export default SearchField